import { Col } from "react-bootstrap";

export interface BlogItemProps {
    blogItem: BlogItemInterface,
    index: Number,
}

export interface BlogItemInterface {
    title: String,
    description: String,
    image: String,
}

export default function BlogItem(props: BlogItemProps) {
    const { blogItem, index } = props;

    return(
        <Col lg={index === 0 ? "8" : "4"}>
            <div className="blog-card" style={{backgroundImage: "url("+blogItem.image+")"}}>
                <div className="blog-card-title">{blogItem.title}</div>
                <div className="blog-card-description">{blogItem.description}</div>
            </div>
        </Col>
    );
}
