import React, { useState } from 'react';
import { Navbar, Nav } from 'react-bootstrap';

import logo from '../assets/img/logo.png';

export default function Header() {

    return(
        <Navbar bg="white" variant="light" expand="lg" sticky="top" className="navbar-main justify-content-between">
            <Navbar.Collapse>
                <div className="container">
                    <Nav className="mr-auto">
                        <Nav.Link>Прокат автодомов в Москве</Nav.Link>
                        <Nav.Link href="tel:8 800 123-45-67">8 800 123-45-67</Nav.Link>
                    </Nav>
                    <Nav className="m-auto">
                        <img src={logo} className="navbar-logo"/>
                    </Nav>
                    <Nav className="ml-auto">
                        <Nav.Link>Путешествия</Nav.Link>
                        <Nav.Link>О нас</Nav.Link>
                        <Nav.Link>Контакты</Nav.Link>
                    </Nav>
                </div>
            </Navbar.Collapse>
        </Navbar>
    )
}