import { Col } from "react-bootstrap";

export interface TextCardItem {
    title: String,
    text: String,
}

export interface TextCardProps {
    card: TextCardItem
}

export default function TextCard(props: TextCardProps) {
    const { card } = props;

    return(
        <Col>
            <div className="TextCard">
                <div className="TextCard-title">{card.title}</div>
                <div className="TextCard-description">{card.text}</div>
            </div>
        </Col>
    );
}