import { Container, Row } from "react-bootstrap";
import BlogItem from "./BlogItem";
import blog1 from "../assets/img/blog1.png";
import blog2 from "../assets/img/blog2.png";
import blog3 from "../assets/img/blog3.png";
import blog4 from "../assets/img/blog4.png";
import blog5 from "../assets/img/blog5.png";

export default function BlogBlock() {
    const blogItems = [
        {
            title: "Отель, который всегда с вами",
            description: "Описание данной статьи в блоге будет размещено тут.",
            image: blog1,
        },
        {
            title: "Для пожилых",
            description: "Описание данной статьи в блоге будет размещено тут.",
            image: blog2,
        },
        {
            title: "Сколько стоит аренда",
            description: "Описание данной статьи в блоге будет размещено тут.",
            image: blog3,
        },
        {
            title: "На выходные в Сочи",
            description: "Описание данной статьи в блоге будет размещено тут.",
            image: blog4,
        },
        {
            title: "Золотое кольцо",
            description: "Описание данной статьи в блоге будет размещено тут.",
            image: blog5,
        },
    ];

    return(
        <div className="BlogBlock">
            <Container>
                <div className="h2 text-primary">Идеи путешествий</div>
                <Row>
                    {blogItems.map((item, index) => <BlogItem blogItem={item} index={index}/>)}
                </Row>
            </Container>
        </div>
    );
}