import { Col } from "react-bootstrap";
import sell from "../assets/img/sell.png"

export interface SellItemProps {
    sellItem: SellItemInterface
}

export interface SellItemInterface {
    title: String,
    price: Number|String,
    description: String,
}

export default function SellItem(props: SellItemProps) {
    const { sellItem } = props;
    return(
        <Col>
            <div className="sell-card">
                <div className="sell-card-image" style={{backgroundImage: "url("+sell+")"}}>
                    <div className="sell-card-title">{sellItem.title}</div>
                </div>
                <div className="sell-card-body">
                    <div className="sell-card-price">
                        <span className="sell-card-price-text">От </span>
                        <span className="sell-card-price-value">{sellItem.price}₽</span>
                    </div>
                    <div className="sell-card-description">{sellItem.description}</div>
                </div>
            </div>
        </Col>
    );
}
