import { Col } from "react-bootstrap";
import rent from "../assets/img/rent.png"

export interface RentItemProps {
    rentItem: RentItemInterfacce
}

export interface RentItemInterfacce {
    title: String,
    price: Number,
    bedPlaces: Number,
    transmission: String,
    gasoline: String,
}

export default function RentItem(props: RentItemProps) {
    return(
        <Col>
            <div className="rent-card">
                <div className="rent-card-image" style={{backgroundImage: "url("+rent+")"}}>
                    <div className="rent-card-title">{props.rentItem.title}</div>
                </div>
                <div className="rent-card-body">
                    <div className="rent-card-price">
                        <span className="rent-card-price-text">От </span>
                        <span className="rent-card-price-value">{props.rentItem.price}₽</span>
                        <span className="rent-card-price-text">/сутки</span>
                    </div>
                    <div className="rent-card-tech">
                        <div className="rent-card-tech-icon"><span className="mdi mdi-account-group-outline"></span></div>
                        <div className="rent-card-tech-text">{props.rentItem.bedPlaces} спальных мест</div>
                    </div>
                    <div className="rent-card-tech">
                        <div className="rent-card-tech-icon"><span className="mdi mdi-car-shift-pattern"></span></div>
                        <div className="rent-card-tech-text">{props.rentItem.transmission}</div>
                    </div>
                    <div className="rent-card-tech">
                        <div className="rent-card-tech-icon"><span className="mdi mdi-engine"></span></div>
                        <div className="rent-card-tech-text">{props.rentItem.gasoline}</div>
                    </div>
                </div>
            </div>
        </Col>
    );
}
