import { Container, Row } from "react-bootstrap";
import TextCard from "./TextCard";
import { TextCardItem } from "./TextCard";
import about from "../assets/img/about.png";

export interface TextCardBlockProps {
    cards: Array<TextCardItem>,
    title: String,
}

export default function TextCardsBlock(props: TextCardBlockProps) {
    const { title, cards } = props;
    console.log(about);

    return(
        <div className="TextCards" style={{backgroundImage: "url("+about+")"}}>
            <Container>
                <div className="block-title h2 text-primary">{title}</div>
                <Row>
                    {cards.map((card, index) => <TextCard card={card} key={index}/>)}
                </Row>
            </Container>
        </div>
    );
}