import { Container, Row } from "react-bootstrap";
import RentItem from "./RentItem";

export default function RentBlock() {
    const rentItem = {
        title: "Кемпер Пруссия 23",
        price: 10000,
        bedPlaces: 5,
        transmission: "АКПП 6 ступенчатая",
        gasoline: "Дизель"
    };
    const rentItems = Array(3).fill(rentItem);

    return(
        <div className="RentBlock bg-light-blue">
            <Container>
                <div className="h2 text-primary">Прокат</div>
                <Row>
                    {rentItems.map(item => <RentItem rentItem={item}/>)}
                </Row>
            </Container>
        </div>
    );
}