import { Container, Row } from "react-bootstrap";
import SellItem from "./SellItem";

export default function SellBlock() {
    const sellItem = {
        title: "Кемпер Пруссия 23",
        price: 4500000,
        description: "Для 2х человек, АКПП, Дизель",
    };
    const sellItems = Array(3).fill(sellItem);

    return(
        <div className="SellBlock bg-light-blue">
            <Container>
                <div className="h2 text-primary">Автодома в продаже</div>
                <Row>
                    {sellItems.map(item => <SellItem sellItem={item}/>)}
                </Row>
            </Container>
        </div>
    );
}